<template>
    <div class="bp-select" :class="[{'select-disabled': disabled},classNames]">
        <div class="bp-select-title" @click="toggleShow">
            <span>{{choosed_text}}</span>
            <svg width="20" height="20" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" :class="icon_class" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M8.988 14.6L2.415 8.312a1.313 1.313 0 010-1.916c.553-.53 1.45-.53 2.004 0L10 11.735l5.581-5.338c.554-.53 1.45-.53 2.004 0 .553.529.553 1.387 0 1.916l-6.573 6.286a1.478 1.478 0 01-2.024 0z" fill="#57565F" fill-rule="evenodd"/></svg>
        </div>
        <ul :class="show ? 'bp-option-group' : 'd-none'">
            <bpOption v-for="optionData in options_data" :key="optionData.text" :text="optionData.text" :choosed_value="choosed_text" :click_event="optionData.click_event" @chooseOption="changeLanguage" @click.native="close"></bpOption>
        </ul>
    </div>
</template>
<script>
import bpOption from "./bp-option.vue"
export default {
    components: {
        bpOption
    },
    props: {
        icon_class: {
            type: String,
            default: "svg-icon"
        },
        classNames: String,
        choosed_value: String,
        text: {
            type: String,
            default: ''
        },
        options_data: Array
    },
    data: function() {
        return {
            disabled: false,
            show: false,
            choosed_text: ''
        }
    },
    methods: {
        toggleShow() {
            if(!this.disabled) {
                this.show = !this.show
            }
        },
        changeLanguage(value) {
            if (this.choosed_value) {
                this.choosed_text = value
            }
        },
        close() {
            this.show = false
        }
    },
    mounted() {
        // document.addEventListener("click", e => {
        //     if(!this.$el.contains(e.target)) {
        //         this.show = false
        //     }
        // })
        if (this.choosed_value) {
            this.choosed_text = this.choosed_value
        } else {
            this.choosed_text = this.text
        }
    }
}
</script>
